import React from 'react';
import './dayboard.css';
import save from './save.svg'

let students = [' .', ' .', ' .', ' .', ' .', ' .', ' .', ' .', ' .', ' .', ' .', ' .', ' .']

export default class Fines extends React.Component {

    render() {
        return (
            <table>
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th> </th>
                        <th>   </th>
                        <th>  </th>
                        <th>   </th>
                        <th>  </th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {students.map((item, index) => <tr key={index}>
                        <td>{index + 1}</td>
                        <td>{item}</td>
                        <td>
                            <select>
                                <option> </option>
                                <option>  </option>
                                <option>- </option>
                            </select>
                        </td>
                        <td>0</td>
                        <td>
                        <input type="number" placeholder="0" step="1" min="0" max="100" />
                        </td>
                        <td>200</td>
                        <td>400</td>
                        <td>
                            <img className="nav-icons" src={save} alt="save" />
                        </td>
                    </tr>)}
                </tbody>
            </table>

        )
    }
}


