import React from 'react';
import save from './save.svg'

let students = [' .', ' .', ' .', ' .', ' .', ' .', ' .', ' .', ' .', ' .', ' .', ' .', ' .']

export default class Dayboard extends React.Component {
    render() {
        return (
            <table>
                <thead>
                    <tr>
                        <th className="first-column"></th>
                        <th></th>
                        <th></th>
                        <th> (1)</th>
                        <th>  (1)</th>
                        <th> -    (1)</th>
                        <th>  (1)</th>
                        <th>  </th>
                        <th>- (100)</th>
                        <th> FB</th>
                        <th>   </th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {students.map((item, index) => <tr key={index}>
                        <td className="first-column">{index + 1}</td>
                        <td>{item}</td>
                        <td>
                            <input type="checkbox" />
                        </td>
                        <td>
                            <input type="checkbox" />
                        </td>
                        <td>
                            <input type="checkbox" />
                        </td>
                        <td>
                            <input type="checkbox" />
                        </td>
                        <td> <input type="checkbox" /></td>
                        <td>
                            <input type="number" placeholder="0" step="1" min="0" max="5" />
                        </td>
                        <td>
                            <input type="checkbox" />
                        </td>
                        <td>
                            <input type="text" />
                        </td>
                        <td>
                            15
                        </td>
                        <td>
                            <img className="nav-icons" src={save} alt="save" />
                        </td>
                    </tr>)}
                </tbody>
            </table>
        )
    }
}


